___CSS_LOADER_EXPORT___.push([module.id, `.compatibility-box {
  width: 80vw;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.compatibility-box h1 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 20px;
}

.sections-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
}

.input-section-wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex: 1;
}

.input-user-section,
.select-target-section {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.select-target-section {
  margin-top: 35px;
}
.input-user-section h2,
.select-target-section h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.user-name-input,
.sex-select,
.menubar-select,
.menubar-select-right {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 10px;
}

button {
  padding: 10px;
  font-size: 1em;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.right-gif-box {
  flex: 1;
  background-color: #ede6e6;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.submit-button {
  display: block;
  width: 30%;
  padding: 15px;
  font-size: 1.2em;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #218838;
}
