___CSS_LOADER_EXPORT___.push([module.id, `.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: linear-gradient(
    45deg,
    #ff0047,
    #2c34c7,
    #00d4ff,
    #ffef00,
    #ff0047
  );
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
}

.login-box {
  background: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 600px;
  width: 100%;
  text-align: center;
}

.loginTitle {
  margin-top: 2vh;
  padding: 10px 20px;
  /* font-family: "Arial", sans-serif; */
  font-size: 34px;
  color: #00bbff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5); /* 텍스트에 그림자 추가 */
  border-radius: 4px;
  display: inline-block; /* 블록 요소로 설정 */
}
.input-group {
  margin-bottom: 10px;
  text-align: left;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.submit-btn {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.login-buttons-container {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.google-login-btn,
.naver-login-btn,
.nomal-login-btn {
  flex: 1;
  margin: 0 5px; /* 버튼 간격을 위해 좌우 마진 추가 */
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.nomal-login-btn {
  background-color: rgb(47, 206, 255);
}
.google-login-btn img,
.naver-login-btn img {
  display: inline-block;
  vertical-align: middle;
}

.recovery-options {
  display: flex;
  justify-content: space-between;
  font-family: "Arial", sans-serif;
}

.recovery-options button {
  background: none;
  border: none;
  color: rgb(47, 206, 255);
  cursor: pointer;
  padding: 0;
  margin-top: 10px;
}
@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
