___CSS_LOADER_EXPORT___.push([module.id, `/* styles.css */
.Recover-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 20px;
}
.Recover-section h1 {
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 20px;
}
.Recover-box {
  display: flex;
  justify-content: space-between;
  width: 100%; /* 전체 너비를 사용 */
  margin-top: 20px;
}
.Recover-box h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

section {
  flex: 1; /* 각 섹션에 동일한 공간 배분 */
  padding: 20px;
  margin: 0 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.input-group-recover-id,
.input-group-recover-pw {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
}
.input-group-recover-id input,
.input-group-recover-pw input {
  padding: 15px;
  font-size: 1.2em;
  border: 1px solid #ccc;
  border-radius: 4px;
}
.user-id-button,
.user-pw-button {
  padding: 15px;
  font-size: 1.2em;
  background-color: #00ff48;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.user-id-button:hover,
.user-pw-button:hover {
  background-color: #0056b3;
}
.error-message {
  color: red;
  margin-top: 10px;
  font-size: 1em;
}`, "",{"version":3,"sources":["webpack://./src/components/recoverUserInfo/recover.css"],"names":[],"mappings":"AAAA,eAAe;AACf;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,kBAAkB;EAClB,gBAAgB;AAClB;AACA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,8BAA8B;EAC9B,WAAW,EAAE,cAAc;EAC3B,gBAAgB;AAClB;AACA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,OAAO,EAAE,oBAAoB;EAC7B,aAAa;EACb,cAAc;EACd,wCAAwC;EACxC,uBAAuB;AACzB;;AAEA;;EAEE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,mBAAmB;AACrB;AACA;;EAEE,aAAa;EACb,gBAAgB;EAChB,sBAAsB;EACtB,kBAAkB;AACpB;AACA;;EAEE,aAAa;EACb,gBAAgB;EAChB,yBAAyB;EACzB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,sCAAsC;AACxC;;AAEA;;EAEE,yBAAyB;AAC3B;AACA;EACE,UAAU;EACV,gBAAgB;EAChB,cAAc;AAChB","sourcesContent":["/* styles.css */\n.Recover-section {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n  margin-top: 20px;\n}\n.Recover-section h1 {\n  text-align: center;\n  font-size: 2.5em;\n  margin-bottom: 20px;\n}\n.Recover-box {\n  display: flex;\n  justify-content: space-between;\n  width: 100%; /* 전체 너비를 사용 */\n  margin-top: 20px;\n}\n.Recover-box h2 {\n  font-size: 1.5em;\n  margin-bottom: 10px;\n}\n\nsection {\n  flex: 1; /* 각 섹션에 동일한 공간 배분 */\n  padding: 20px;\n  margin: 0 10px;\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\n  background-color: white;\n}\n\n.input-group-recover-id,\n.input-group-recover-pw {\n  display: flex;\n  flex-direction: column;\n  gap: 15px;\n  margin-bottom: 20px;\n}\n.input-group-recover-id input,\n.input-group-recover-pw input {\n  padding: 15px;\n  font-size: 1.2em;\n  border: 1px solid #ccc;\n  border-radius: 4px;\n}\n.user-id-button,\n.user-pw-button {\n  padding: 15px;\n  font-size: 1.2em;\n  background-color: #00ff48;\n  color: #fff;\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n}\n\n.user-id-button:hover,\n.user-pw-button:hover {\n  background-color: #0056b3;\n}\n.error-message {\n  color: red;\n  margin-top: 10px;\n