.searchwrap {
  display: flex;
  align-items: center; /* 세로 방향 중앙 정렬 */
  justify-content: flex-start;
  padding: 10px; /* 패딩 */
  margin: 40px;
}

.lefttitle {
  margin-right: 10px; /* 오른쪽에 간격 제공 */
  /* 타이틀 스타일 */
  font-weight: bold;
}

.searchbox {
  margin-left: 100px;
  flex-grow: 1; /* 나머지 공간을 채우도록 설정 */
  padding: 5px; /* 편안한 입력을 위한 패딩 */
  border: 1px solid #ccc; /* 테두리 */
  border-radius: 4px; /* 둥근 모서리 */
}

.write-button {
  margin-left: auto;
  /* 기존 스타일 유지 */
  background-color: #42d538;
  color: white; /* 텍스트 색상 */
  padding: 15px 30px; /* 상하, 좌우 패딩 */
  border: none; /* 테두리 없음 */
  border-radius: 5px; /* 모서리 둥글게 */
  cursor: pointer; /* 마우스 포인터를 손 모양으로 변경 */
}
.write-button:hover {
  background-color: gray; /* 마우스 오버 시 배경색 변경 */
}
.search-button {
  /* 기존 스타일 유지 */
  background-color: #42d538;
  color: white; /* 텍스트 색상 */
  padding: 15px 30px; /* 상하, 좌우 패딩 */
  border: none; /* 테두리 없음 */
  border-radius: 5px; /* 모서리 둥글게 */
  cursor: pointer; /* 마우스 포인터를 손 모양으로 변경 */
}

.search-button:hover {
  background-color: gray; /* 마우스 오버 시 배경색 변경 */
}
