___CSS_LOADER_EXPORT___.push([module.id, `.non-social-login-box {
  width: 50vw;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  padding: 40px;
  margin-top: 100px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.non-social-form-box {
  width: 50vw;
  height: 50vh;
}
.non-social-login-box form {
  display: flex;
  flex-direction: column;
}

.input-group {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.input-group input,
.input-group button {
  padding: 10px;
  margin-right: 5px; /* 입력 필드와 버튼 사이의 간격 */
  border-radius: 20px; /* 둥근 모서리 */
  border: 1px solid #ccc;
}

.input-group input {
  flex-grow: 1; /* 입력 필드가 가능한 많은 공간을 차지하도록 설정 */
}

.input-group button {
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.input-group button:hover {
  background-color: #0056b3;
}

.submit-btn {
  width: 100%;
  /* 기타 버튼 스타일 유지 */
}
