___CSS_LOADER_EXPORT___.push([module.id, `.profile-update {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.77;
}

.profile-update__box {
  background-color: green;
  width: 50vw;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-update__title {
  font-size: 1.5em;
  margin-bottom: 20px;
}

.profile-update__section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-update__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.profile-update__input {
  width: 80%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.profile-update__button {
  padding: 10px 20px;
  background-color: #00ff22;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.profile-update__button:hover {
  background-color: #0056b3;
}

.profile-update__verification-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.profile-update__verification-input {
  width: 80%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.profile-update__verification-button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.profile-update__verification-button:hover {
  background-color: #218838;
}

.profile-update__timer {
  margin-top: 10px;
  color: #dc3545;
}

.profile-update__error {
  margin-top: 10px;
  color: #dc3545;
}
